/* src/components/Home.css */

.home-container {
    text-align: center;
    padding: 20px;
  }
  
  .button-container {
    position: relative;
    display: inline-block;
  }
  
  .click-me-button {
    padding: 20px 40px;
    font-size: 24px;
    cursor: pointer;
    background-color: #ff6f61;
    color: #fff;
    border: none;
    border-radius: 10px;
    animation: pulse 1s infinite;
  }

  .options-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .option-button {
    font-size: 2.5rem;
    padding: 20px 40px;
    margin: 10px;
    border: none;
    border-radius: 50%;
    background-color: #3498db;
    color: white;
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease;
    font-weight: bold;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 150px;
    height: 150px;
  }
  
  .option-button:hover {
    transform: scale(1.1);
    background-color: #2980b9;
  }
  
  .option-button:active {
    transform: scale(1.05);
  }
  
  .potter {
    animation: rotate 1.5s infinite linear;
  }
  
  .dnote {
    animation: rotate 1.5s infinite linear reverse;
  }
  
  
  @keyframes pulse {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
    100% {
      transform: scale(1);
    }
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .pointer {
    position: absolute;
    font-size: 30px;
    animation: bounce 1s infinite;
  }
  
  .left-pointer {
    top: 50%;
    left: -50px;
    transform: translateY(-50%);
  }
  
  .right-pointer {
    top: 50%;
    right: -50px;
    transform: translateY(-50%);
  }
  
  
  .video-container {
    margin-top: 20px;
  }
  
  .video-player {
    width: 100%;
    max-width: 800px;
  }
  
  .skip-button {
    display: block;
    margin: 20px auto;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    background-color: #ff6f61;
    color: #fff;
    border: none;
      }  